<template>
  <div class="project-container" v-loading="isLoading" ref="container">
    <div class="project">
      <h2>项目和效果</h2>
      <RouterLink
        v-for="item in data"
        :key="item.id"
        :to="{
          name: 'Demo',
          params: {
            url: item.url,
          },
        }"
        class="item"
      >
        <img :src="item.thumb" :alt="item.name" class="item-img" />
        <div>
          <h4>项目&效果名称:{{ item.name }}</h4>
          <div class="github">github地址: {{ item.github }}</div>
          <div>
            <span
              class="description"
              v-for="(description, i) in item.description"
              :key="i"
              >{{ description }}</span
            >
          </div>
        </div>
      </RouterLink>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import setScroll from "@/mixins/setScroll";
export default {
  mixins: [setScroll("container")],
  data() {
    return {};
  },
  computed: {
    ...mapState("demo", ["data", "isLoading"]),
  },
  created() {
    this.$store.dispatch("demo/featchDemo");
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.project-container {
  color: rgb(63, 52, 52);
  height: 100%;
  width: 100%;
  position: relative;
  .project {
    height: 100%;
    width: 100%;
    h2 {
      height: 10%;
      display: flex;
      align-items: center;
      box-sizing: border-box;
      padding-left: 10%;
      color: #008c8c;
    }
    a {
      margin-top: 0.2em;
      display: flex;
      padding: 5px;
      padding-left: 2%;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(0, 0, 0, 0);
      &:hover {
        color: inherit;
        border-bottom: 2px solid rgba(0, 0, 0, 0.5);
        box-sizing: border-box;
        transform: translateY(-2px);
        transition: 0.5s;
      }
      img {
        height: 100px;
        width: 150px;
      }
      & > div {
        margin-left: 1em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 0.95em;
      }
    }
  }
}
</style>