import Vuex from 'vuex';
import Vue from 'vue';
import global from './global';
import banner from './banner';
import about from './about';
import demo from './demo';


Vue.use(Vuex);
const store = new Vuex.Store({
    strict: true, //严格模式
    modules: {
        global,
        banner,
        about,
        demo,
    },
});
export default store;