import Home from '@/views/Home';
import About from '@/views/About';
import Project from '@/views/Project';
import Blog from '@/views/Blog';
import Message from '@/views/Message';
import Demo from '@/views/Project/Demo';
import Details from '@/views/Blog/components/Details';
const routes = [
    { name: 'Home', path: '/', component: Home, meta: { title: '主页', } },
    { name: 'About', path: '/about', component: About, meta: { title: '关于我', } },
    { name: 'Demo', path: '/project/demo/:url', component: Demo, meta: { title: '项目和效果详情', } },
    { name: 'Project', path: '/project', component: Project, meta: { title: '项目和效果', } },
    { name: 'Blog', path: '/article', component: Blog, meta: { title: '文章', } },
    //动态路由
    { name: 'CategoryBlog', path: '/article/cate/:categoryId', component: Blog, meta: { title: '文章分类', } },
    { name: 'Blogdetails', path: '/article/:detailId', component: Details, meta: { title: '文章详情', } },

    { name: 'Message', path: '/message', component: Message, meta: { title: '留言板', } },
]
export default routes;